.case-type-container {
  position: relative;
  .case-type-title {
    margin-top: -12px;
    text-align: center;
    color: #08e2ff;
    font-size: 50px;
  } 
  .case-list {
    h2 {
      font-size: 40px;
      color: #fff;
      background: url(./images/title-bg.png) no-repeat left bottom;
      width: 1119px;
      height: 70px;
      line-height: 50px;
      display: inline-block;
    }
    /*现场勘察*/
    .survey {
      background: url(./images/rate-bg.png) no-repeat;
      width: 1045px;
      height: 107px;
      line-height: 95px;
      padding: 0 40px 0 40px;
      margin: 15px 0 15px 0;
      span {
        color: #cacaca;
        font-size: 30px;
        &:nth-child(2),
        &:nth-child(3){
          font-size: 48px;
          color: #03f0ff;
          padding: 0 200px;
          font-family: digifacewide;
        }
        &:nth-child(3){
          color: #fd6703;
          padding-left: 0;
        }
      }
      i {
        display: inline-block;
        width: 27px;
        height: 24px;
      }
      .icon-down {
        background: url(./images/down.png) no-repeat;
      }
    }
    /*提取率*/
    .extract{
      margin: 15px 0 15px 0;
      float: left;
      li {
        background: url(./images/rate-bg2.png) no-repeat;
        width: 247px;
        height: 97px;
        line-height: 85px;
        float: left;
        margin-right: 10px;
        font-size: 30px;
        color: #cacaca;
        padding-left: 20px;
        span {
          color: #fd6703;
          font-size: 36px;
          padding-left: 40px;
          font-family: digifacewide;
        }
      }
    }
    .case {
      margin: 15px 0 15px 0;
      float: left;
      div {
        width: 229px;
        float: left;
        margin-right: 180px;
        padding-top: 140px;
        ul {
          background: url(./images/bg-2.png) no-repeat;
          width: 205px;
          height: 199px;
          font-size: 26px;
          color: #cacaca;
          line-height: 46px;
          padding-left: 20px;
          padding-top: 10px;
          span {
            width: 140px;
            display: inline-block;
          }
        }
      }
      .person {
        background: url(./images/person.png) no-repeat;
        li {
          line-height: 86px;
        }
      }
      .dna {
        background: url(./images/dna.png) no-repeat;
      }
      .fingerprint {
        background: url(./images/fingerprint.png) no-repeat;
        margin-right: 0;
        li {
          line-height: 62px;
        }
      }
    }
  }
}


