.subject-container {
  position: relative;
  margin-top: 20px;
  .pie-grid {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -43%);
    width: 576px;
    height: 576px;
    background: url(./images/grid.png) no-repeat center / 100% 100%;
  }
  .subject-title {
    color: #08e2ff;
    font-size: 50px;
    text-align: center;
  }
}


